import React from "react"
import Button from '@material-ui/core/Button';
import styles from "./skills.module.scss"
import { Link } from "gatsby"
import TrimmedContainer from "./trimmed-container";
import CSharp from "../assets/c-sharp.svg"
import JScript from "../assets/javascript.svg"
import TScript from "../assets/typescript-icon.svg"
import HTML5 from "../assets/html-5.svg"
import CSS3 from "../assets/css-3.svg"
import SASS from "../assets/sass.svg"
import SQL from "../assets/SQL-Databases.svg"
import Dart from "../assets/dart.svg"
import Bash from "../assets/bash.svg"

const Skills = () => {


    return (
        <TrimmedContainer>
            <div>
                <h2 className={styles.heading}>My Current Skills</h2>

                <div>
                    <h3>Languages</h3>
                    <p>I started to program using C# back in 2003 and picked up new languages as I progressed throughout my career. <br /> It is amazing to see how these languages developed over the years and just gets better by the day.</p>

                    <div className={styles.techlogos}>

                        <figure className={styles.setheight}
                            data-sal="zoom-in"
                            data-sal-delay="100"
                            data-sal-easing="ease">
                            <CSharp />
                            <figcaption>C#</figcaption>
                        </figure>


                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="200"
                            data-sal-easing="ease">
                            <JScript />
                            <figcaption>JavaScript</figcaption>
                        </figure>


                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="300"
                            data-sal-easing="ease">
                            <TScript />
                            <figcaption>TypeScript</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="400"
                            data-sal-easing="ease">
                            <Dart />
                            <figcaption>Dart</figcaption>
                        </figure>

                        <figure className={styles.setheight}
                            data-sal="zoom-in"
                            data-sal-delay="450"
                            data-sal-easing="ease">
                            <HTML5 />
                            <figcaption>HTML 5</figcaption>
                        </figure>

                        <figure className={styles.setheight}
                            data-sal="zoom-in"
                            data-sal-delay="500"
                            data-sal-easing="ease">
                            <CSS3 />
                            <figcaption>CSS 3</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="550"
                            data-sal-easing="ease">
                            <SASS />
                            <figcaption>SASS</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="600"
                            data-sal-easing="ease">
                            <SQL />
                            <figcaption>T-SQL</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="650"
                            data-sal-easing="ease">
                            <Bash />
                            <figcaption>Bash</figcaption>
                        </figure>




                    </div>

                    <h3>Frontend</h3>
                    <p>
                        I love all the goodness that frameworks such as Angular, React and Vue brings to the table, yet, I am a strong believer in understanding the fundamentals and patterns that shape these frameworks.
                    <br />
                        Lately, I have started using Web Components and believe they hold a bright feuture.
                    </p>


                    {/* <h3>Backend</h3>
                    ASP.net
                    ASP.net Core
                    MS SQL Server


                    <h3>Devops</h3> */}


                </div>
            </div>
        </TrimmedContainer >
    );
};

export default Skills;